.page-intro {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  min-width: 365px;
  min-height: 500px;
  align-items: center; }
  .page-intro .app__heading {
    align-items: flex-start; }
  .page-intro .app__row {
    width: 100%;
    justify-content: center; }
  .page-intro__grid {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 1160px;
    padding-bottom: 110px;
    flex-wrap: wrap; }
    .page-intro__grid .card {
      flex: 1;
      padding: 0 16px;
      width: 33%; }
  .page-intro__new-doc {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    flex: 1;
    max-width: 90%;
    padding: 0 16px;
    text-align: center; }
    .page-intro__new-doc__image {
      height: 75px;
      margin-top: 20px; }
    .page-intro__new-doc span {
      font-size: 14px;
      padding: 0 20px; }
    .page-intro__new-doc .spc-button {
      margin: 0 2px;
      font-size: 14px;
      padding: 0 10px; }

@media (max-width: 767px) {
  .page-intro__grid .card {
    width: 50%;
    min-width: 50%; }
  .page-intro__new-doc {
    height: 263.5px; } }
