/*:=========  Spectrum Palette :========= */

/*
	Spectrum "Light 245"
	Color order: light -> dark
	Always keep in HEX format since it's easiest to manipulate with builtin Stylus functions
*/

$color-red-1: #E44853;
$color-red-2: #D83742;
$color-red-3: #C92533;
$color-red-4: #BA1225;

$color-green-1: #0F996D;
$color-green-2: #09875F;
$color-green-3: #117957;
$color-green-4: #136B4D;

$color-blue-1: #1884EC;
$color-blue-2: #1473E6;
$color-blue-3: #0F66D0;
$color-blue-4: #0E59B9;

$color-yellow-1: #E1951D;
$color-yellow-2: #D3891B;
$color-yellow-3: #C57E19;
$color-yellow-4: #B67317;

$color-grey-1: #FFFFFF;
$color-grey-2: #FAFAFA;
$color-grey-3: #F5F5F5;
$color-grey-4: #EAEAEA;
$color-grey-5: #E1E1E1;
$color-grey-6: #B6B6B6;
$color-grey-7: #8E8E8E;
$color-grey-8: #707070;
$color-grey-9: #4B4B4B;
$color-grey-10: #2C2C2C;

/*
	Spectrum "Dark 50"
	Color order: dark -> light
	Order reversed so that theming is as simple as adding "dark" to the variable, rather than switching numbers around.
*/

$color-dark-red-1: #E44853;
$color-dark-red-2: #EE5A66;
$color-dark-red-3: #F76C7A;
$color-dark-red-4: #FF7D8F;

$color-dark-green-1: #0F996D;
$color-dark-green-2: #14AB7B;
$color-dark-green-3: #1ABD89;
$color-dark-green-4: #20CF97;

$color-dark-blue-1: #1884EC;
$color-dark-blue-2: #1D94F3;
$color-dark-blue-3: #21A5F9;
$color-dark-blue-4: #26B5FF;

$color-dark-yellow-1: #E1951D;
$color-dark-yellow-2: #EE9F20;
$color-dark-yellow-3: #F9AB24;
$color-dark-yellow-4: #FFBB29;

$color-dark-grey-0: #1B1B1B;
$color-dark-grey-1: #282828;
$color-dark-grey-2: #2D2D2D;
$color-dark-grey-3: #323232;
$color-dark-grey-4: #393939;
$color-dark-grey-5: #3E3E3E;
$color-dark-grey-6: #5A5A5A;
$color-dark-grey-7: #7B7B7B;
$color-dark-grey-8: #999999;
$color-dark-grey-9: #CDCDCD;
$color-dark-grey-10: #FCFCFC;

// it's easier to use these because they contain the "color" keyword and therefore show up in searches
$color-black: #000000;
$color-white: #FFFFFF;
