/* Typography */
.ccfiles-empty {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center; }
  .ccfiles-empty__heading {
    font-weight: 100;
    font-style: normal;
    font-size: 32px; }
  .ccfiles-empty__title {
    font-weight: 100;
    font-style: normal;
    font-size: 24px; }
  .ccfiles-empty__subtitle {
    width: 500px;
    font-size: 18px; }
  .ccfiles-empty__logo {
    opacity: 0.4; }
  .ccfiles-empty__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
    max-width: 500px; }
    .ccfiles-empty__card:first-child {
      margin-left: 0; }
    .ccfiles-empty__card svg {
      width: 80px;
      height: auto; }
    .ccfiles-empty__card > * {
      margin-bottom: 24px; }
  .ccfiles-empty__buttons .spc-button {
    box-sizing: border-box;
    margin-top: 20px; }
  .ccfiles-empty__buttons .spc-button--quiet {
    margin-right: 10px; }

.spc--dark .ccfiles-empty__card svg {
  fill: white; }
