@keyframes fade-in-flex {
  from {
    opacity: 0;
    display: none; }
  to {
    opacity: 1;
    display: flex; } }

@keyframes fade-in-search {
  0% {
    opacity: 0;
    height: 0; }
  5% {
    opacity: 0;
    height: 100%; }
  100% {
    opacity: 1;
    height: 100%; } }

@keyframes fade-out-search {
  0% {
    opacity: 1;
    height: 100%; }
  95% {
    opacity: 0;
    height: 100%; }
  100% {
    opacity: 0;
    height: 0; } }

.search {
  cursor: default; }
  .search__container {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    height: 0;
    transition: all 150ms linear;
    justify-content: center;
    flex-direction: column;
    background: #FFFFFF;
    visibility: hidden;
    /* &--outro {
			animation: fade-out-search 300ms linear;
			animation-fill-mode: forwards;
		} */ }
    .search__container--active {
      animation: fade-in-search 200ms linear;
      animation-fill-mode: forwards;
      z-index: 1480;
      visibility: visible; }
  .search__bar {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 600px;
    height: 64px;
    transition: all 150ms linear; }
  .search__bar::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    opacity: 0;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease-in-out; }
  .search__bar--hanging {
    background-color: #FAFAFA; }
  .search__bar--hanging.search__bar::after {
    opacity: 1;
    height: 64px; }
  .search__error-message {
    max-width: 600px;
    margin-top: 10vh; }
    .search__error-message__image {
      width: 150px;
      margin: auto;
      fill: #8E8E8E; }
    .search__error-message__text {
      text-align: center;
      color: #707070;
      font-style: italic;
      font-size: 14px;
      max-width: 330px; }
    .search__error-message__title {
      text-align: center;
      font-size: 24px; }
  .search__exit {
    cursor: pointer;
    z-index: 10;
    transition: color 150ms linear;
    display: flex;
    align-items: center; }
    .search__exit svg {
      margin-right: 10px;
      fill: #707070;
      transition: fill 150ms linear; }
    .search__exit:hover {
      color: #000000; }
      .search__exit:hover svg {
        fill: #000000; }
  .search__result-container {
    flex: 1; }
  .search__results {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 50px; }
  .search__block {
    width: 80%;
    margin-bottom: 42px; }
    .search__block__grid .card {
      width: 165px;
      padding: 0;
      margin: 0 15px 15px 0; }
    .search__block__header {
      margin-bottom: 14px;
      padding: 4px 8px 0 8px; }
      .search__block__header__title {
        display: inline-block;
        color: #4B4B4B;
        font-size: 16px; }
        .search__block__header__title .inapp-icons {
          margin-right: 4px; }
          .search__block__header__title .inapp-icons:before {
            font-size: 18px;
            vertical-align: top; }
      .search__block__header__meta {
        display: inline-block;
        text-align: right;
        float: right;
        color: #8E8E8E;
        font-size: 15px;
        margin-right: 8px; }
        .search__block__header__meta__link {
          margin-left: 5px;
          color: #4B4B4B;
          cursor: pointer;
          transition: color 150ms linear; }
          .search__block__header__meta__link svg {
            transition: fill 150ms linear; }
          .search__block__header__meta__link:hover {
            color: #000000; }
            .search__block__header__meta__link:hover svg {
              fill: #000000; }
      .search__block__header__breadcrumb {
        color: #8E8E8E;
        cursor: pointer;
        transition: color 200ms linear; }
        .search__block__header__breadcrumb:hover {
          color: #4B4B4B;
          transition: color 200ms linear; }
        .search__block__header__breadcrumb__divider {
          margin: 0 15px;
          color: #8E8E8E;
          cursor: default; }
          .search__block__header__breadcrumb__divider .spc-icons {
            font-size: 9px; }
        .search__block__header__breadcrumb--active {
          color: #4B4B4B;
          cursor: default; }
  .search__title-icon {
    width: 18px;
    height: 18px;
    margin-right: 10px;
    fill: #8E8E8E; }
    .search__title-icon svg {
      /* re-align icons, was being set to middle by spectreui */
      vertical-align: text-bottom; }
  .search__title-icon-ccfiles {
    fill: #4B4B4B; }
  .search__title-icon-stock {
    fill: #b3b3b3; }
  .search__title-icon-learn svg {
    /* this one looks better at this alignment */
    vertical-align: text-top; }
  .search__field-bumper {
    flex: 1;
    display: flex;
    align-items: center;
    height: 100%; }
    .search__field-bumper--left {
      padding-left: 20px; }
      .search__field-bumper--left .inapp-icon--left {
        margin-right: 10px;
        padding: 4px;
        border-radius: 50%;
        color: #4B4B4B;
        border: 2px solid #4B4B4B; }
    .search__field-bumper--right {
      text-align: left;
      padding-left: 15px;
      color: #8E8E8E; }
  .search__field-wrapper {
    position: relative;
    flex: 2;
    width: 60%;
    z-index: 2;
    /* bring it above the ::after dropshadow */
    /*&__search-terms {
			position: absolute;
			background: $color-dark-grey-2;
			width: 100%;
			box-sizing: border-box;
			display: none;

			&__term {
				cursor: pointer;
				padding: 5px 10px;

				&:hover {
					background: $color-dark-grey-1;
				}
			}
		}*/ }
    .search__field-wrapper .spc-textfield {
      width: 100%;
      margin: 0; }
    .search__field-wrapper .spc-textfield__input {
      text-indent: 0;
      padding-left: 34px; }
    .search__field-wrapper .spc-icons {
      position: absolute;
      top: 8px;
      left: 11px;
      font-size: 1em;
      color: #4B4B4B;
      z-index: 1500; }
    .search__field-wrapper .spc-spinner__dots {
      position: absolute;
      top: 4px;
      left: 5px;
      width: 24px;
      height: 24px;
      z-index: 1500; }
    .search__field-wrapper .listbox {
      position: absolute;
      background: #FFFFFF;
      color: #4B4B4B;
      width: 100%;
      box-sizing: border-box;
      margin: 0;
      padding: 0;
      display: none;
      top: 40px;
      border-radius: 4px; }
      .search__field-wrapper .listbox:focus {
        outline: none;
        display: block; }
      .search__field-wrapper .listbox--active {
        display: block; }
    .search__field-wrapper .listbox__options {
      border: 1px solid #B6B6B6;
      border-radius: 4px;
      list-style: none;
      padding: 0;
      margin: 0;
      overflow: hidden; }
    .search__field-wrapper .listbox__item {
      cursor: pointer;
      padding: 5px 25px;
      list-style-type: none;
      border-left: 2px solid transparent;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap; }
      .search__field-wrapper .listbox__item:hover {
        background: #FAFAFA;
        color: #2C2C2C;
        transition: color 200ms linear; }
      .search__field-wrapper .listbox__item--selected {
        background: #FAFAFA;
        color: #1884EC;
        border-color: #1884EC; }
  .search__instruct {
    font-size: 12px;
    color: #B6B6B6;
    z-index: 1500;
    word-spacing: 0.03em; }
    .search__instruct--inner {
      position: absolute;
      top: 8px;
      right: 11px; }
    .search__instruct__key {
      border: 1px solid;
      border-radius: 4px;
      padding: 0px 4px 1px 4px;
      margin: 0px 1px; }

.spc-textfield__input:focus + .search__field-wrapper__search-terms, .search__field-wrapper__search-terms:hover {
  display: block; }

.learn-result {
  overflow: hidden;
  padding: 10px 4px; }
  .learn-result .card__external-icon {
    width: 14px;
    height: 14px; }
  .learn-result .learn-result__wrapper {
    max-width: 680px; }
  .learn-result .learn-result__title {
    font-size: 15px;
    color: #4B4B4B;
    margin-bottom: 10px;
    transition: color 150ms linear; }
    .learn-result .learn-result__title svg {
      transition: fill 150ms linear; }
    .learn-result .learn-result__title:hover {
      cursor: pointer;
      color: #000000; }
      .learn-result .learn-result__title:hover svg {
        fill: #000000; }
  .learn-result .learn-result__description {
    color: #8E8E8E;
    font-size: 14px; }
  .learn-result .learn-result__date {
    color: #B6B6B6;
    font-size: 13px;
    margin-top: 6px; }
  .learn-result .learn-result__image {
    float: left;
    max-height: 120px;
    margin-right: 20px; }
    .learn-result .learn-result__image:hover {
      cursor: pointer; }

