.card {
  box-sizing: border-box;
  padding: 0 15px 15px 0;
  transition: all 150ms ease-out; }
  .card__content {
    border-radius: 4px;
    background-color: #F5F5F5;
    transition: all 125ms ease-in-out;
    cursor: pointer; }
    .card__content:hover {
      box-shadow: 0 0 0 1px #999999;
      /*  dark shadow for light and vice versa */ }
    .card__content:active {
      box-shadow: 0 0 0 1px #8E8E8E;
      /* same for light and dark */ }
  .card__info {
    box-sizing: border-box;
    overflow: hidden;
    padding: 12px 16px 13px;
    border-top: 1px solid #FFFFFF; }
  .card__title {
    font-size: 14px; }
  .card__subtitle {
    font-size: 12px; }
  .card__thumbnail {
    position: relative;
    overflow: hidden;
    border-radius: 4px 4px 0 0;
    width: 100%;
    height: 0px;
    padding-bottom: 56.25%;
    /* 16:9 */ }
    .card__thumbnail__img--contain-landscape {
      width: 100%;
      height: auto; }
    .card__thumbnail__img--contain {
      width: auto;
      height: 100%; }
    .card__thumbnail img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0); }
  .card__extra {
    padding-left: 5px;
    padding-top: 2px;
    font-size: 12px;
    text-transform: uppercase;
    color: #8E8E8E; }
  .card--truncate-text {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
  .card__video-overlay {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);
    top: 0;
    border-radius: 4px 4px 0 0; }
    .card__video-overlay:hover .card__video-overlay__img-play {
      opacity: 0.8; }
    .card__video-overlay__img-play {
      opacity: 0.65;
      transition: opacity 150ms ease-in-out; }
      .card__video-overlay__img-play path {
        fill: #fff; }
  .card__external-icon {
    width: 15px;
    height: 15px;
    margin: 0 0 3px 5px;
    fill: #707070; }

.spc--dark .card__content {
  background-color: #222; }

.spc--dark .card__content:hover {
  box-shadow: 0 0 0 1px #707070; }

.spc--dark .card__extra {
  color: #999999; }

.spc--dark .card__title {
  color: #FFFFFF; }

.spc--dark .card__info {
  border-color: #1B1B1B; }

.spc--dark .card__external-icon {
  fill: #999999; }

/* LEARN TAB */
@media (max-width: 645px) {
  .tab-learn__grid .card {
    width: 80%; } }

@media (min-width: 645px) {
  .tab-learn__grid .card {
    width: 45%; } }

