@mixin anim-normal() {
	transition: all 150ms linear;
}

@keyframes fade-in-flex {
	from {
		opacity: 0;
		display: none;
	}

	to {
		opacity: 1;
		display: flex;
	}
}

@keyframes fade-in-search {
	0% {
		opacity: 0;
		height: 0;
	}

	5% {
		opacity: 0;
		height: 100%;
	}

	100% {
		opacity: 1;
		height: 100%;
	}
}

@keyframes fade-out-search {
	0% {
		opacity: 1;
		height: 100%;
	}

	95% {
		opacity: 0;
		height: 100%;
	}

	100% {
		opacity: 0;
		height: 0;
	}
}