@keyframes fade-in-flex {
  from {
    opacity: 0;
    display: none; }
  to {
    opacity: 1;
    display: flex; } }

@keyframes fade-in-search {
  0% {
    opacity: 0;
    height: 0; }
  5% {
    opacity: 0;
    height: 100%; }
  100% {
    opacity: 1;
    height: 100%; } }

@keyframes fade-out-search {
  0% {
    opacity: 1;
    height: 100%; }
  95% {
    opacity: 0;
    height: 100%; }
  100% {
    opacity: 0;
    height: 0; } }

.header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 100%;
  min-width: 500px;
  background: #F5F5F5; }
  .header__left {
    display: flex;
    flex: 1;
    justify-content: flex-start;
    z-index: 1300; }
  .header__center {
    display: flex;
    justify-content: center;
    align-self: flex-end;
    z-index: 1300; }
  .header__right {
    display: flex;
    flex: 1;
    justify-content: flex-end;
    z-index: 1300; }
  .header__search {
    display: flex;
    align-items: center;
    margin-right: 25px;
    cursor: pointer;
    transition: all 150ms linear; }
    .header__search .spc-icons {
      font-size: 1.2em;
      outline: none; }
    .header__search:hover {
      color: #2C2C2C; }
  .header::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 64px;
    opacity: 0;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
    transition: opacity 0.3s ease-in-out; }
  .header--hanging.header::after {
    opacity: 1; }
  .header .spc-tabs__tab {
    font-size: 18px;
    padding-bottom: 10px; }

.spc--dark .header {
  background: #323232; }
  .spc--dark .header__search:hover {
    color: #FCFCFC; }

[modality=keyboard] .header__search .spc-icons:focus {
  color: #0F66D0; }

[modality=keyboard].spc--dark .header__search .spc-icons:focus {
  color: #1884EC; }
