.cc-photos {
  display: none; }
  .cc-photos.cc-photos-visible {
    display: block; }

[modality=keyboard] .tab-work__sort-button:focus {
  color: #0F66D0; }

[modality=keyboard].spc--dark .tab-work__sort-button:focus {
  color: #1884EC; }
