.product-rune {
  flex: none;
  margin-left: 20px; }
  .product-rune span {
    margin-left: 10px;
    letter-spacing: 0.01em;
    font-size: 18.5px;
    color: #4B4B4B; }
  .product-rune div {
    cursor: pointer;
    color: #4B4B4B; }
  .product-rune__icon {
    width: 36px;
    height: auto; }

.spc--dark .product-rune span {
  color: #CDCDCD; }
