/* Typography */
@keyframes fade-in-flex {
  from {
    opacity: 0;
    display: none; }
  to {
    opacity: 1;
    display: flex; } }

@keyframes fade-in-search {
  0% {
    opacity: 0;
    height: 0; }
  5% {
    opacity: 0;
    height: 100%; }
  100% {
    opacity: 1;
    height: 100%; } }

@keyframes fade-out-search {
  0% {
    opacity: 1;
    height: 100%; }
  95% {
    opacity: 0;
    height: 100%; }
  100% {
    opacity: 0;
    height: 0; } }

.spc-layout {
  background: #FFFFFF; }

* {
  user-select: none; }

.spc-keyboard *:focus {
  outline-color: #0F66D0; }

.app {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .app__tab {
    flex: 1; }
  .app__container {
    display: flex;
    flex-direction: column;
    flex: 1;
    position: relative; }
    .open-mode .app__container {
      max-height: inherit; }
  .app__container--hide {
    visibility: hidden; }
  .app__row {
    display: flex;
    flex-direction: row; }
  .app__column {
    display: flex;
    flex-direction: column; }
  .app__content-column {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
    max-width: 1460px;
    min-width: 450px;
    margin-right: 100px;
    /* $sidebar-width; */
    margin-left: 230px;
    margin-bottom: 80px; }
  .app__heading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 110px;
    margin: 0;
    font-weight: 100;
    font-style: normal;
    text-align: center;
    cursor: default; }
  .app__actions {
    float: left; }
    .app__actions > .spc-button {
      float: left;
      clear: both;
      margin-bottom: 15px; }
  .app__video-container {
    width: 854px; }
  .app__video-player {
    width: 100%;
    border-radius: 4px; }
    .app__video-player::-webkit-media-controls-fullscreen-button {
      display: none; }
  .app .modal__overlay {
    z-index: 1480; }
  .app .spc-tabs__tab {
    margin-right: 60px; }
    .app .spc-tabs__tab--last {
      margin-right: 0; }

.spc-spinner__dots {
  background-image: url("../images/indicators/dots.gif");
  background-size: cover; }

.spc-layout.spc--dark {
  background: #1B1B1B; }

.spc-layout.spc-keyboard.spc--dark *:focus {
  outline-color: #21A5F9; }

.spc--dark .spc-spinner__dots {
  background-image: url("../images/indicators/dots-dark.gif"); }

@media (min-width: 1920px) {
  .app__content-column {
    margin-left: auto;
    margin-right: auto; } }
