.footer {
  position: fixed;
  bottom: 0;
  transform: translate3d(0, 0, 0);
  height: 74px;
  width: 100%;
  opacity: 1;
  background-color: #FAFAFA;
  transition: all 500ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
  /* easeInOutQuad */ }
  .footer__inner {
    box-sizing: border-box;
    margin: 0 auto;
    padding-top: 4px;
    height: 100%; }

.footer--collapsed {
  transform: translate(0px, 100%);
  opacity: 0; }

.spc--dark .footer {
  background-color: #292929; }
