.breadcrumb {
  align-items: stretch;
  height: 100%;
  width: 80%;
  margin: auto; }

.breadcrumb__nav {
  height: 40px;
  line-height: 40px;
  font-size: 13px; }

.breadcrumb__nav__item {
  cursor: pointer;
  color: #2C2C2C; }

.spc-layout.spc--dark .breadcrumb__nav__item {
  color: #FCFCFC; }
