/* Typography */
.empty-recent {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center; }
  .empty-recent__heading {
    font-weight: 100;
    font-style: normal;
    font-size: 32px; }
  .empty-recent__title {
    font-weight: 100;
    font-style: normal;
    font-size: 24px; }
  .empty-recent__subtitle {
    width: 500px;
    font-size: 18px; }
  .empty-recent__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
    margin-top: 80px; }
    .empty-recent__card .spc-button {
      margin-top: 20px; }
    .empty-recent__card > * {
      margin-bottom: 24px; }
  .empty-recent__image {
    width: auto;
    height: 92px; }

