/*:=========  Spectrum Palette :========= */
/*
	Spectrum "Light 245"
	Color order: light -> dark
	Always keep in HEX format since it's easiest to manipulate with builtin Stylus functions
*/
/*
	Spectrum "Dark 50"
	Color order: dark -> light
	Order reversed so that theming is as simple as adding "dark" to the variable, rather than switching numbers around.
*/
/* Typography */
.menu-panel {
  z-index: 1;
  position: absolute;
  top: 0;
  right: 0;
  min-width: 250px;
  background-color: #F5F5F5;
  padding: 40px 28px 22px 28px;
  border-radius: 4px;
  outline: none; }

container-templates .menu-panel {
  right: auto;
  left: 0; }

debug-menu .menu-panel--toggle {
  color: red; }

.menu-panel--toggle {
  display: inline-block;
  appearance: none;
  padding: 0px;
  border-width: 0;
  background: none;
  cursor: pointer;
  text-align: center;
  width: 24px;
  height: 24px;
  cursor: pointer;
  line-height: 24px;
  margin-right: 20px;
  transition: color 100ms ease-in-out; }

.menu-panel--item {
  line-height: 2em;
  padding: 3px 5px;
  font-size: 13px; }

.menu-panel--row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; }

.menu-panel--spacer {
  height: 15px; }
  .menu-panel--spacer:after {
    content: '';
    display: table;
    clear: both; }

.spc--dark .menu-panel {
  background-color: #282828; }

.spc--dark debug-menu .menu-panel--toggle {
  color: gold; }
