.panel-recent {
  width: 100%; }
  .panel-recent__grid {
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    align-self: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%; }
    .panel-recent__grid .card {
      width: 208px;
      transition: none; }
  .panel-recent__actions {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column; }
    .panel-recent__actions button {
      margin-bottom: 15px; }
  .panel-recent__filter {
    position: absolute;
    top: 110px;
    right: 130px; }
  .panel-recent__search-wrapper {
    position: absolute;
    top: 100px;
    left: 56px; }
    .panel-recent__search-wrapper .spc-textfield {
      width: 160px; }
    .panel-recent__search-wrapper .spc-icons {
      position: absolute;
      top: 10px;
      left: -20px;
      font-size: 14px;
      color: #7B7B7B; }

