/*:=========  Spectrum Palette :========= */
/*
	Spectrum "Light 245"
	Color order: light -> dark
	Always keep in HEX format since it's easiest to manipulate with builtin Stylus functions
*/
/*
	Spectrum "Dark 50"
	Color order: dark -> light
	Order reversed so that theming is as simple as adding "dark" to the variable, rather than switching numbers around.
*/
.upsell {
  display: flex;
  flex-direction: row;
  align-items: center;
  transition-duration: .9s;
  transition-timing-function: ease;
  margin: 0 auto;
  padding: 0 16px;
  width: 580px;
  height: 64px;
  border-radius: 4px;
  opacity: 1;
  background-color: #F5F5F5; }
  .upsell__mainline {
    color: #4B4B4B;
    line-height: 1;
    margin-right: 10px; }
  .upsell__subline {
    color: #707070;
    margin-top: 4px;
    font-size: 87.5%; }

.stock-card {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center; }
  .stock-card__search-wrapper {
    position: relative;
    margin-right: 20px; }
    .stock-card__search-wrapper .spc-icons {
      position: absolute;
      top: 10px;
      left: 0px;
      font-size: 16px;
      color: #B6B6B6; }
  .stock-card form {
    display: flex;
    flex-direction: direction;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    padding: 10px; }
    .stock-card form .spc-textfield {
      width: 300px;
      margin-bottom: 0; }
  .stock-card .spc-textfield--quiet .spc-textfield__input {
    color: #B6B6B6;
    border-color: #B6B6B6;
    font-size: 16px;
    padding-left: 26px; }
  .stock-card .spc-textfield__input:hover::placeholder {
    color: #E1E1E1; }

.spc--dark .upsell {
  background-color: #343434; }
  .spc--dark .upsell__mainline {
    color: #CDCDCD; }
  .spc--dark .upsell__subline {
    color: #999999; }
  .spc--dark .upsell .spc-textfield--quiet .spc-textfield__input {
    border-color: #B6B6B6; }
  .spc--dark .upsell .spc-textfield__input::placeholder {
    color: #B6B6B6; }
  .spc--dark .upsell .spc-textfield__input:hover::placeholder {
    color: #E1E1E1; }
